.row{
    display: inline-block;
    /* border : 1px solid red; */
    height: 100%;
    width: auto;
    /* white-space: nowrap; */
    padding: 10px;
}



.slider1{
    animation : slider 20s linear infinite;
    /* transform: translateX(-100%); */
}

.slider2{
    animation : slider 24s linear infinite;
}




@keyframes slider{
    0%{
        transform: translateX(0%);
    }
    
    100%{
        transform: translateX(-100%);
    }
}


